$color_1: inherit;
$color_2: #000;
$color_3: #171717;
$font-family_1: Cinzel;
$font-family_2: "Neue Haas Grotesk Display Pro";
$background-color_1: #fff;

.logo-title a{
	color:$color_3;
	text-decoration: none;
}

.nav-item {
	text-decoration: none;
	// align-self: stretch;
	// flex: 1;
	position: relative;
	text-transform: uppercase;
	color: $color_1;
}
.nav-links-wrapper {
	position: absolute;
	height: 14%;
	width: 57.01%;
	top: 43%;
	right: 17.95%;
	bottom: 43%;
	left: 25.03%;
	display: flex;
	flex-direction: row;
	align-items: center;
	justify-content: flex-start;
	gap: 259px;
}
.envelope-icon {
	position: absolute;
	top: 33px;
	right: 96px;
	width: 34px;
	height: 34px;
}
.hamburger-icon {
	position: absolute;
	top: calc(50% - 6px);
	right: 253px;
	width: 54px;
	height: 12px;
	display: none;
}
.logo__title__text {
	margin: 0;
}
.logo-title {
	position: relative;
	top: calc(50% - 14px);
	left: 0;
	text-transform: uppercase;
}
.logo-wrapper {
	position: absolute;
	top: 2px;
	left: 100px;
	width: 232px;
	height: 98px;
	overflow: hidden;
	font-size: 18px;
	color: $color_2;
	font-family: $font-family_1;
}
.navbar {
	position: absolute;
	top: 109px;
	left: 0;
	width: 1440px;
	height: 100px;
}
.header {
	width: 100%;
	position: relative;
	height: 318px;
	overflow: hidden;
	text-align: left;
	font-size: 12px;
	color: $color_3;
	font-family: $font-family_2;
	
}
@media screen and (max-width: 1200px) {
	.nav-links-wrapper {
		width: 704px;
		left: 300px;
	}
	.navbar {
		width: 1200px;
	}
}
@media screen and (max-width: 960px) {
	.nav-links-wrapper {
		display: none;
	}
	.hamburger-icon {
		display: flex;
		left: 670px;
	}
	.header {
		width: 960px;
	}
	.navbar {
		width: 960px;
	}
}
@media screen and (max-width: 420px) {
	
	.header {
		width: 420px;
	}
	
	.envelope-icon {
		width: 34px;
		height: 34px;
		left: 296px;
		top: 36px;
	}
	.hamburger-icon {
		width: 43px;
		height: 10px;
		left: 238px;
	}
	.logo-title {
		left: 0;
		top: 35px;
	}
	.logo-wrapper {
		width: 110px;
		left: 100px;
		top: 0;
	}
	.navbar {
		width: 420px;
	}
}

.nav-item:nth-child(1){
	left: 0;
}



.nav-item:nth-child(2){
	left: 47px;
}



.nav-item:nth-child(3){
	left: 72px;
}



.offcanvas__menu {
height: 100vh;
position: absolute;
top: 0;
left: 0;
bottom: 0;
right: 0;
background: #171717;
z-index: 10;
}


.offcanvas-social-icons-wrapper a{
	text-decoration: none;
	color:var(--color-white);
}