:root {
	/* fonts */
	--h2: "Neue Haas Grotesk Display Pro";
	--h1: Cinzel;
  
	/* font sizes */
	--p-size: 12px;
	--font-size-5xs: 8px;
	--font-size-9xl: 28px;
	--font-size-base: 16px;
	--h2-size: 40.5px;
	--h1-size: 136.7px;
	--font-size-83xl: 102px;
	--font-size-37xl: 56px;
  
	/* Colors */
	--color-white: #fff;
	--color-gray-100: #242424;
	--color-gray-200: #171717;
	--color-black: #000;
	--color-dimgray-100: #5e5e5e;
  
	/* Gaps */
	--gap-49xl: 68px;
	--gap-21xl: 40px;
	--gap-5xl: 24px;
	--gap-10xs: 3px;
  
	/* Paddings */
	--padding-3xs: 10px;
	--padding-281xl: 300px;


	/*GSAP Variables*/
	--after : 100%;
	--before : 0;
  }
  

  @-webkit-keyframes rotating {
    from{
        -webkit-transform: rotate(0deg);
    }
    to{
        -webkit-transform: rotate(360deg);
    }
}


.strong{
	position: relative;
}


*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
	mix-blend-mode: normal;

}
.c-scrollbar{
	z-index: -1;
}


.intro-text-wrapper .intro-text{
line-height: 1.4;
}


body{
	background: var(--color-white);

	
	
}
.play-video1.primary__btn::before,
.play-video1.primary__btn::after{
	background: #fff;
}



.primary__btn::before{
	content: "";
	position: absolute;
	bottom: 0;
	left: 0;
	background: #171717;
	width: var(--before);
	height: 1px;
	transition: width 0.5s;
}

.footer .primary__btn::before,
.footer .primary__btn::after{
	background: #ffffff;
}

.primary__btn::after{
	content: "";
	position: absolute;
	bottom: 0;
	right: 0;
	background: #171717;
	width: var(--after);
	height: 1px;
	transition: width 0.5s;
}



.primary__text::before{
	content: "";
	display: inline-flex;
	position: absolute;
	bottom: 0;
	left:0;
	background: #171717;
	width: var(--before);
	height: 1px;
}


.primary__text::after{
	content: "";
	display: inline-flex;
	position: absolute;
	bottom: 0;
	right:0;
	background: #171717;
	width: var(--after);
	height: 1px;
}




.line__1,
.line__2,
.line__3{

position: fixed;
top: 0;
height: 100%;
width: 1px;
border: 1px solid #e9e9e9;

    
}

.line__1{
left: calc(100% - 75%);
}

.line__2{

left: calc(100% - 49.2%);
}


.line__3{
left: calc(100% - 24.8%);
}



.rotate__title > div:nth-child(1) span{
	will-change: transform;
	transform-style: preserve-3d;
	transition: 0.5s;
	transition-delay: calc(0.05s * var(--index));
	transform-origin: bottom;
    display: inline-block;	

}

.rotate__title > div:nth-child(2) span{
	will-change: transform;
	transform-style: preserve-3d;
	transition: 0.5s;
	transition-delay: calc(0.05s * var(--index));
	transform-origin: top;
    display: inline-block;	
	transform: translate3d(0,100%,0) rotateX(-90deg);

}

.rotate__title:hover > div:nth-child(1) span{
	transform: translate3d(0,-100%,0) rotateX(-90deg);
}

.rotate__title:hover > div:nth-child(2) span{
	transform: translate3d(0,0%,0) rotateX(0deg);
}

.intersect{
	overflow: hidden;
}

.intersect img{
	clip-path: inset(50% 0% 50% 0%);
	
}




	

@media screen and (max-width: 420px){

.line__1{
left: 105px;
}

.line__2{

left: 210px;
}


.line__3{
left: 315px;
}




}