$color_1: #fff;
$font-family_1: "Neue Haas Grotesk Display Pro";
$font-family_2: Cinzel;
$background-color_1: #171717;

.offcanvas-navlink-item {
	align-self: stretch;
	flex: 1;
	position: relative;
	text-transform: uppercase;
}
.offcanvas-nav-wrapper {
	position: absolute;
	height: 68.04%;
	width:32.05%;
	top: 15.98%;
	right: 47.14%;
	bottom: 15.98%;
	left: 23.81%;
	overflow: hidden;
	display: flex;
	flex-direction: column;
	align-items: flex-start;
	justify-content: center;
	padding: 29px 0;
	box-sizing: border-box;
	gap: 74px;
}
.offcanvas-close-icon {
	position: absolute;
	height: 6.3%;
	width: 6.19%;
	top: 23.73%;
	right: 23.1%;
	bottom: 69.98%;
	left: 70.71%;
	max-width: 100%;
	overflow: hidden;
	max-height: 100%;
}
.offcanvas-social-icons-wrapper {
	position: absolute;
	height: 5.06%;
	top: 71.43%;
	bottom: 23.51%;
	left: calc(50% + 12px);
	width: 98px;
	overflow: hidden;
	display: flex;
	flex-direction: row;
	align-items: center;
	justify-content: flex-end;
	padding: 10px 0;
	box-sizing: border-box;
	gap: 10px;
	text-align: right;
	font-size: 8px;
	font-family: $font-family_1;
}
.offcanvas-menu {
	width: 100%;
	position: absolute;
    top: 0;
    left: 0;
	background-color: $background-color_1;
	height: 0;
	overflow: hidden;
	text-align: left;
	font-size: 21px;
	color: $color_1;
	font-family: $font-family_2;
    z-index: 10;
}

.offcanvas-nav-wrapper .offcanvas-navlink-item{
	color: #fff;
}