.footer .get-in-touch {
    cursor: pointer;
    border: 0;
    padding: 0;
    background-color: transparent;
    height: 14px;
    position: relative;
    font-size: 12px;
    text-transform: uppercase;
    font-family: "Neue Haas Grotesk Display Pro";
    color: #fff;
    text-align: center;
    display: inline-block;
  }
  
  .footer .get-in-touch-btn {
    position: absolute;
    height: 2.2%;
    top: 15.58%;
    bottom: 91.65%;
    left: calc(50% - 40px);
    width: 80px;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
  }
  
  .footer-title {
    margin: 0;
    align-self: stretch;
    // flex: 1;
    position: relative;
    font-size: inherit;
    font-weight: 400;
    font-family: inherit;
  }
  
  .footer-title-wrapper {
    position: absolute;
    height: 22.53%;
    width: 100%;
    top: 27.34%;
    right: 0;
    bottom: 50.13%;
    left: 0;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    text-align: center;
    font-size: 136.7px;
    font-family: Cinzel;
  }
  
  .location-icon {
    align-self: stretch;
    width: 12px;
    position: relative;
    max-height: 100%;
  }
  
  .ratakatu-29-a {
    // flex: 1;
    position: relative;
    text-transform: uppercase;
  }
  
  .address-wrapper {
    flex: 1;
    height: 30px;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    gap: 7px;
  }
  
  .envlope-icon {
    width: 18px;
    position: relative;
    height: 13px;
  }
  
  .address-wrapper1 {
    align-self: stretch;
    flex: 1;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    gap: 4px;
  }
  
  .globe-icon {
    width: 14px;
    position: relative;
    height: 14px;
  }
  
  .footer-top {
    position: absolute;
    height: 21.42%;
    width: 86.04%;
    top: 56.84%;
    right: 6.98%;
    bottom: 21.75%;
    left: 6.98%;
    overflow: hidden;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    padding: 60px 0;
    box-sizing: border-box;
    gap: 165px;
  }
  
  .copyright-icon {
    width: 14px;
    position: relative;
    height: 14px;
  }
  
  .copyright-wrapper {
    align-self: stretch;
    flex: 1;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-start;
    gap: 3px;
  }
  
  .register-icon {
    width: 16px;
    position: relative;
    height: 16px;
  }
  
  .register-wrapper {
    align-self: stretch;
    flex: 1;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-start;
    gap: 4px;
  }
  
  .facebook-svgrepocom-icon {
    align-self: stretch;
    flex: 1;
    position: relative;
    max-width: 100%;
    overflow: hidden;
    max-height: 100%;
  }
  
  .footer-social-icons-wrapper {
    width: 126px;
    height: 28px;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-end;
    gap: 20px;
  }
  
  .footer {
    width: 100%;
    overflow: hidden;
  }
  
  .inner-footer {
    width: 100%;
    overflow: hidden;
    position: absolute;
    height: 8.99%;
    top: 91.01%;
    right: 0;
    bottom: 0;
    left: 0;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-start;
    padding: 21px 100px;
    box-sizing: border-box;
    gap: 270.3px;
    border-top: 1px  solid  #242424;
  }
  
  .footer {
    position: relative;
    background-color: #171717;
    height: 790px;
    text-align: left;
    font-size: 12px;
    color: #fff;
    font-family: "Neue Haas Grotesk Display Pro";
  }
  
  @media screen and (max-width: 1200px) {

    .lets-talk-wrapper{
      width: 80.67%;
    }
    .footer-title-wrapper {
      width: 1200px;
    }
  
    .footer-top {
      width: 1000px;
      gap: 79px;
      left: 100px;
    }
  
    .inner-footer {
      width: 1200px;
    }
  }
  
  @media screen and (max-width: 960px) {
  

    .footer .get-in-touch-btn {
      width: 82px;
    }
  
    .footer-title {
      font-size: 102px;
    }
  
    .footer-title-wrapper {
      width: 960px;
      height: 178px;
      top: 172px;
    }
  
    .address-wrapper, .address-wrapper1 {
      flex: unset;
      align-self: stretch;
      justify-content: flex-start;
    }
  
    .footer-top {
      width: 761px;
      height: 331px;
      flex-direction: column;
      gap: 46px;
      left: 99px;
      top: 298px;
    }
  
    .copyright-wrapper, .register-wrapper {
      flex: unset;
      align-self: stretch;
    }
  
    .inner-footer {
      width: 960px;
      height: 198px;
      flex-direction: column;
      gap: 18px;
      align-items: flex-start;
      justify-content: center;
      top: 592px;
     
    }
  }
  
  @media screen and (max-width: 420px) {
   .footer .get-in-touch {
      font-size: 8px;
      
    }
    .footer .get-in-touch-btn {
      height: 2.1%;
      
    }
  
    
    .footer-title {
      font-size: 56px;
    }
  
    .footer-title-wrapper {
      width: 420px;
    }
  
    .ratakatu-29-a {
      font-size: 8px;
    }
  
    .footer-social-icons-wrapper {
      gap: 10px;
    }


 

  }
  

.footer-title div span:nth-child(6){
  opacity: 0;
}

.footer-top a{
  text-decoration: none;
  color: #fff;
}