$color_1: #080808;
$color_2: #000;
$font-family_1: "Neue Haas Grotesk Display Pro";
$font-family_2: inherit;
$font-family_3: Cinzel;
$background-color_1: transparent;
$background-color_2: #fff;

.name-field-child {
	align-self: stretch;
	position: relative;
	border-top: 1px solid #080808;
	box-sizing: border-box;
	height: 1px;
}
.your-name {
	align-self: stretch;
	position: relative;
	border: 0;
	outline: 0;
	font-family: $font-family_1;
	font-size: 12px;
	background-color: $background-color_1;
	text-transform: uppercase;
	color: $color_1;
	text-align: left;
}
.name-field {
	flex: 1;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: flex-start;
	gap: 13px;
}
.field-1 {
	align-self: stretch;
	display: flex;
	flex-direction: row;
	align-items: center;
	justify-content: flex-start;
	gap: 20px;
}
.phone {
	align-self: stretch;
	border: 0;
	outline: 0;
	font-family: $font-family_1;
	font-size: 12px;
	background-color: $background-color_1;
	position: relative;
	text-transform: uppercase;
	color: $color_1;
	text-align: left;
}
.field-3 {
	align-self: stretch;
	display: flex;
	flex-direction: column;
	align-items: flex-start;
	justify-content: center;
	padding: 15px 0;
	gap: 13px;
}
.input-fields {
	align-self: stretch;
	display: flex;
	flex-direction: column;
	align-items: flex-start;
	justify-content: center;
	gap: 40px;
}
.send-message {
	// align-self: stretch;
	display: flex;
	align-items: center;
	// width: 122px;
	position: relative;
	font-size: 12px;
	text-transform: uppercase;
	font-family: $font-family_1;
	color: $color_1;
	white-space: pre-wrap;
	text-align: left;
	flex-shrink: 0;
}
.send-message-wrapper {
	align-self: stretch;
	display: flex;
	align-items: center;
	cursor: pointer;
	border: 0;
	padding: 0;
	background-color: $background-color_1;
	height: 27px;
	flex-direction: row;
	justify-content: flex-start;
}
.contact-form {
	flex: 1;
	height: 394px;
	display: flex;
	flex-direction: column;
	align-items: flex-start;
	justify-content: center;
	gap: 66px;
}
.need-help {
	margin: 0;
	line-height: 1.5;
}
.lorem-ipsum-dolor-container {
	align-self: stretch;
	position: relative;
	text-transform: uppercase;
	flex: 1;
	font-size: 12px;
	color: $color_2;
}
.need-help-container {
	align-self: stretch;
	position: relative;
	text-transform: uppercase;
	margin: 0;
	height: 82px;
	font-size: inherit;
	font-weight: 400;
	font-family: $font-family_2;
	display: inline-block;
	flex-shrink: 0;
}
.contact-circle-text-icon {
	// align-self: stretch;
	// flex: 1;
	position: relative;
	max-width: 100%;
	overflow: hidden;
	max-height: 100%;
	// object-fit: cover;
	z-index: 0;
	-webkit-animation: rotating 10s linear infinite;
	width: 741px;
    height: 851px;
	
}
.contact-arrow-down-direction-s-icon {
	width: 45.22%;
	position: absolute;
	margin: 0 !important;
	height: 39.13%;
	top: 30.43%;
	right: 27.39%;
	bottom: 30.43%;
	left: 27.39%;
	max-width: 100%;
	overflow: hidden;
	max-height: 100%;
	z-index: 1;
}
.contact-circle-text-wrapper {
	width: 175px;
    height: 175px;
	display: flex;
	flex-direction: row;
	align-items: center;
	justify-content: space-between;
	box-sizing: border-box;
	position: relative;
}
.thinking-wrapper {
	align-self: stretch;
	flex: 1;
	display: flex;
	flex-direction: column;
	align-items: flex-end;
	justify-content: center;
	padding: 10px 8.2px;
	gap: 20px;
}
.contact-wrapper {
	position: absolute;
	height: 38.48%;
	width: 86.74%;
	top: 48.54%;
	right: 6.32%;
	bottom: 12.99%;
	left: 6.94%;
	display: flex;
	flex-direction: row;
	align-items: flex-end;
	justify-content: flex-start;
	gap: 10px;
}
.lets-talk {
	margin: 0;
	height: 184px;
	flex: 1;
	position: relative;
	font-size: inherit;
	font-weight: 400;
	font-family: $font-family_2;
	display: inline-block;
}
.lets-talk-title {
	height: 128px;
	flex-direction: row;
	justify-content: center;
	align-self: stretch;
	display: flex;
	align-items: center;
	line-height: 1;
}
.get-in-touch-text {
	align-self: stretch;
	display: flex;
	align-items: center;
	margin: 0;
	flex: 1;
	position: relative;
	text-transform: uppercase;
	color: #171717;

}
.lets-talk-text {
	// align-self: stretch;
	display: flex;
	align-items: center;
	height: 16px;
	flex-direction: row;
	justify-content: center;
	padding: 0 12px;
	box-sizing: border-box;
	font-size: 12px;
	font-family: $font-family_1;
	position: relative;
}
.lets-talk-wrapper {
	position: absolute;
	height: 21.19%;
	width: 46.67%;
	top: 26.37%;
	right: 6.88%;
	bottom: 52.44%;
	left: 6.46%;
	display: flex;
	flex-direction: column;
	align-items: flex-start;
	justify-content: center;
	text-align: left;
	font-size: 136.7px;
	font-family: $font-family_3;
}
.contact {
	width: 100%;
	position: relative;
	height: 1024px;
	overflow: hidden;
	text-align: right;
	font-size: 40.5px;
	color: $color_1;
	font-family: $font-family_1;
}
@media screen and (max-width: 1200px) {
	.lets-talk-wrapper{
		width: 74.67%;
	}
	.need-help-container {
		font-size: 62px;
	}
	.contact-wrapper {
		gap: 10px;
		align-items: center;
		justify-content: center;
	}
	.lets-talk {
		font-size: 184px;
	}
}
@media screen and (max-width: 960px) {

	.need-help {
		line-height: 1.8;
	}


	.lets-talk-text {
		top: -54px;
	}

	.phone {
		font-size: 12px;
	}
	.contact-form {
		flex: unset;
		align-self: stretch;
	}
	.need-help-container {
		font-size: 28px;
		text-align: left;
	}
	.lorem-ipsum-dolor-container {
		text-align: left;
	}
	.contact-circle-text-wrapper {
		width: 161px;
        height: 155.88px;
		top: 78px;
    
	}
	.contact-wrapper {
		align-items: flex-start;
		justify-content: center;
		box-sizing: border-box;
		width: 832px;
		flex-direction: column;
		gap: 30px;
		padding-left: 36px;
		padding-right: 42px;
		top: 1056px;
	}
	.thinking-wrapper {
		align-items: flex-start;
		justify-content: center;
		box-sizing: border-box;
		padding-left: 0;
		padding-right: 0;
		flex: unset;
		align-self: stretch;
	}
	.lets-talk {
		font-size: 102px;
		
	}
	.lets-talk-wrapper {
		width: 832px;
		height: 379px;
		align-items: flex-start;
		justify-content: center;
		padding-left: 33px;
		padding-right: 33px;
		box-sizing: border-box;
		top: 645px;
		left: 61.57px;
	}
	.contact {
		width: 960px;
		height: 2248px;
	}
}
@media screen and (max-width: 420px) {

	.lets-talk-title{
    width: 400px;

	}

	.lets-talk-text {
        top: -15px;
    }

	.your-name {
		font-size: 8px;
	}
	.name-field {
		flex: unset;
		align-self: stretch;
	}
	.field-1 {
		flex-direction: column;
	}
	.phone {
		font-size: 8px;
	}
	.send-message {
		font-size: 8px;
	}
	.contact-form {
		gap: 16px;
		flex: unset;
		align-self: stretch;
	}
	.need-help-container {
		font-size: 16px;
		text-align: left;
	}
	.lorem-ipsum-dolor-container {
		font-size: 8px;
		text-align: left;
		width: 256px;
	}
	.contact-circle-text-wrapper {
		width: 113px;
        height: 110px;
    
	}
	.thinking-wrapper {
		padding-left: 0;
		padding-right: 0;
		padding-bottom: 10px;
		box-sizing: border-box;
		flex: unset;
		align-self: stretch;
	}
	.contact-wrapper {
		width: 364px;
		height: 830px;
		flex-direction: column;
		padding-left: 69px;
		padding-right: 69px;
		box-sizing: border-box;
		left: 29px;
		top: 336.18px;
	}
	.lets-talk {
		font-size: 56px;
		height: 62px;
	}
	.get-in-touch-text {
		font-size: 8px;
	}
	.lets-talk-text {
		width: 400px;
		padding-left: 0;
		box-sizing: border-box;
		margin-top: -30px;
	}
	.lets-talk-wrapper {
		width: 364px;
		padding-left: 67px;
		box-sizing: border-box;
		top: 36.38px;
		left: 29px;
	}
	.contact {
		width: 420px;
		height: 1236px;
	}
}
